<template>
  <div class="courseDetail">
    <el-button type="primary" size="small" icon="el-icon-arrow-left" @click="turnBack"></el-button>
    <div class="courseDetailWrapper">
      <el-form
        v-loading="loading"
        :rules="rules"
        label-position="right"
        label-width="140px"
        :model="formInline"
        ref="formInline"
      >
        <el-form-item v-if="this.$route.query._id" label="id" prop="_id">
          <span>{{formInline._id}}</span>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input
            class="inputStyle"
            placeholder="10"
            maxlength="10"
            v-model="formInline.name"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="formInline.type">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="level">
          <el-select v-model="formInline.level">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="intro">
          <el-input
            class="inputStyle"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 3}"
            placeholder="50"
            maxlength="50"
            v-model="formInline.intro"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <el-input
            class="inputStyle"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 5}"
            placeholder=""
            v-model="formInline.detail"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="time">
          <el-input-number v-model="formInline.time" :precision="2" :step="1" :max="100"></el-input-number>
          <span class="inputSuffix"></span>
        </el-form-item>
        <el-form-item label="" prop="cover">
          <el-input class="inputStyle" placeholder="" v-model="formInline.cover"></el-input>
        </el-form-item>
        <el-form-item v-if="this.$route.query._id" label="" prop="videos">
          <span>{{formInline.videos ? "" : ""}}</span>
          <el-button
            class="courseVideoBtn"
            type="text"
            @click="toCourseVideo"
          >{{formInline.video ? "" : ""}}</el-button>
        </el-form-item>
        <el-form-item v-if="this.$route.query._id">
          <el-button type="success" @click="updateCourse('formInline')"></el-button>
          <el-button type="danger" @click="delDialogVisible = true"></el-button>
        </el-form-item>
        <el-form-item v-else>
          <el-button type="primary" @click="addNewCourse('formInline')"></el-button>
          <el-button @click="resetForm('formInline')"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-dialog v-loading="loading" title="" :visible.sync="delDialogVisible" width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delDialogVisible = false"> </el-button>
        <el-button type="primary" @click="delCourse"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "@/utils/axios";
import { courses, courseDetail } from "@/utils/api";

export default {
  name: "courseDetail",
  components: {},
  data() {
    const validateTime = (rule, value, callback) => {
      if (value === 0) {
        callback(new Error("0"));
      } else {
        callback();
      }
    };
    return {
      loading: false,
      delDialogVisible: false,
      formInline: {
        name: "",
        type: "",
        level: "",
        intro: "",
        detail: "",
        time: 0,
        cover: "",
        videos: null,
        _id: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "change" }
        ],
        type: [
          { required: true, message: "", trigger: "change" }
        ],
        level: [
          { required: true, message: "", trigger: "change" }
        ],
        intro: [
          { required: true, message: "", trigger: "change" }
        ],
        detail: [
          { required: true, message: "", trigger: "change" }
        ],
        time: [{ validator: validateTime, trigger: "change" }],
        cover: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    turnBack() {
      this.$router.push({ path: `/manage/courses` });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    async addNewCourse(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          let params = JSON.parse(JSON.stringify(this.formInline));
          delete params.videos;
          delete params._id;
          let data = await axios.post(courseDetail.addCourse, params);
          data = data.data;
          if (data.code === 0) {
            this.$message.success(data.msg);
          } else {
            this.$message.error(data.msg);
          }
        } else {
          return false;
        }
      });
    },
    // 
    async updateCourse(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          let params = JSON.parse(JSON.stringify(this.formInline));
          delete params.videos;
          delete params._id;
          delete params.__v;

          let data = await axios.post(courseDetail.updateCourse, params);
          data = data.data;
          if (data.code === 0) {
            this.$message.success(data.msg);
          } else {
            this.$message.error(data.msg);
          }
        } else {
          return false;
        }
      });
    },
    // 
    async delCourse() {
      this.loading = true;
      let data = await axios.post(courseDetail.delCourse, {
        _id: this.formInline._id
      });
      data = data.data;
      if (data.code === 0) {
        this.$message.success(`${data.msg}, 3`, 5);
        setTimeout(() => {
          this.$router.push({ path: `/manage/courses` });
        }, 3000);
      } else {
        this.$message.error(data.msg);
      }
    },
    // 
    async getCourseDetail(_id) {
      let data = await axios.get(courses.getCourseDetail, {
        params: { _id }
      });
      data = data.data;
      if (data.code === 0) {
        this.formInline = data.data;
        this.loading = false;
      }
    },
    // 
    toCourseVideo() {
      let query = {
        _id: this.formInline._id,
        name: this.formInline.name,
        videos: this.formInline.videos
      };
      this.$router.push({ path: `/manage/courseVideos`, query });
    }
  },
  mounted() {
    if (this.$route.query._id) {
      this.loading = true;
      this.getCourseDetail(this.$route.query._id);
    }
  }
};
</script>

<style lang="less" scoped>
.courseDetail {
  .courseDetailWrapper {
    margin-top: 30px;
    .inputStyle {
      width: 30vw;
    }
    .inputSuffix {
      display: inline-block;
      width: 40px;
      height: 39px;
      background-color: #f5f7fa;
      text-align: center;
      border: 1px solid #dcdfe6;
      border-left: 0;
      border-radius: 5px;
    }
    .courseVideoBtn {
      margin-left: 20px;
    }
  }
}
</style>
